import React from 'react';
import { Badge,  Button, Table } from 'react-bootstrap'; 
import PropTypes from 'prop-types';
import moment from 'moment';

export const Pagination = (props) => {
	const { recordCount, pageSize, pageNo } = props.pageDetails;

	if (!recordCount || recordCount <= 0 )
		return null;

	let totalPageNos = Math.floor(recordCount/pageSize);

	if( (recordCount < pageSize) || ( (recordCount > pageSize) && ((recordCount % pageSize) > 1) ))
		totalPageNos+=1


	let arrayNumbers=[1];
	while(arrayNumbers.length < totalPageNos){
		arrayNumbers.push(arrayNumbers.length+1);
	}


	return (<p>{pageSize} records per list.<br/> Page {pageNo} Of {totalPageNos}  GoTo Page <select name="pageNo" onChange={ (e) => props.onPageChange(e) } > {arrayNumbers.map( (item)=> <option key={item} value= {item}> {item} </option> )} </select> </p>);
}


export const SearchSideBar = (props) => {
	return (<Table responsive>
				<thead>
					<tr className="darkblue">
						<th>Select Search List</th>
					</tr>
				</thead>
				<tbody> 
					{ 	props.data.map( (item, index)=> 
							<tr key={index} className="lightblue"> 
								<td onClick={()=>props.onChangeHandler(index)} >
								<Badge style={{cursor: 'pointer'}}> &nbsp;{item.Name}&nbsp;</Badge>
							</td></tr>
						)
					}
				</tbody>
			</Table>
			);
}

export const ShowFilesList = (props) => {
	if ( !props.data ) return null;

	return (<Table responsive striped>
			<thead>
			<tr className="darkblue">
				<th><Button >#</Button></th>
				<th><Button onClick={ () => props.onSortHandler('Path')} >File Path</Button></th>
				<th><Button onClick={ () => props.onSortHandler('Size') } >Size</Button></th>
				<th><Button onClick={ () => props.onSortHandler('Ext') } >File Extension</Button></th>
				<th><Button onClick={ () => props.onSortHandler('MaxDate') } >Last Used Date</Button></th>
			</tr>
			</thead>
			<tbody>
					{ props.data.map( (item, index) => <tr className="oddeven" key={index}>
										<td>{index+1}</td>
										<td className="wordwrap">{item.Path}</td>
										<td>{item.Size}</td>
										<td className="wordwrap">{item.Ext}</td><td>{
											(item.MaxDate ? moment(item.MaxDate).format("YYYY-MMM-DD") : null)
										}</td>
									</tr>
				 			)}
			</tbody>
			</Table>
			);
}

Pagination.propTypes = {
	pageDetails: PropTypes.shape({
			recordCount: PropTypes.number.isRequired,
			pageSize: PropTypes.number.isRequired,
			pageNo: PropTypes.number.isRequired
	})
};

SearchSideBar.propTypes = {
	data: PropTypes.arrayOf(PropTypes.shape({
			Name: PropTypes.string.isRequired
	}))
};