import React, { Component} from 'react';
import { Button, Table } from 'react-bootstrap';
import { toast }  from 'react-toastify';
import Joi from 'joi';
import moment from 'moment';
import PropTypes from 'prop-types';

export default class SearchForm extends Component {
	state = {
		searchFilter : { minFileSize: '',
						 maxFileSize: '',
						 minDate: '',
						 maxDate: '',
						 filePath: '' },
		errors:{}
	}

	schema = {
		minFileSize: Joi.number().integer().allow(null).allow('').optional().label("Min. file size "),
		maxFileSize: Joi.number().integer().allow(null).allow('').optional().label("Max. file size "),
		filePath: Joi.string().allow('').optional()
	}

	constructor(props) {
		super(props);
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange = (e) => {
		const { name, value } = e.currentTarget;
		const searchFilter = {...this.state.searchFilter};
		searchFilter[name] = value;
		this.setState({searchFilter});
	}

	handleSubmit = () => {
		if(this.props.data.Count === 0){
			toast.info("0 results to scan in this search", { autoClose: 2000 });
			return false;
		}

		if ( this.validate() )
		{	
			const currentState = {...this.state.searchFilter};
			this.props.onSubmitHandler(currentState);
		}
		return false;
	}

	validate = ()=> {
		const { minFileSize, maxFileSize, filePath, minDate, maxDate } = this.state.searchFilter
		const result = Joi.validate({ minFileSize, maxFileSize, filePath }, this.schema, {abortEarly: false});
		let errors = "";
		if( result.error )
			errors = result.error.details.map( (item) => item.message).join('.');
		
		if(minDate)
		{
			errors += (moment(minDate, "YYYY-MM-DD", true).isValid()? "": "<br>Invalid Min Last Used date");
		}
		if(maxDate)
		{
			errors += (moment(maxDate, "YYYY-MM-DD", true).isValid() ? "": "Invalid Max Last Used date");
		}
		if(errors){
			toast.error(errors);
			return false;
		}

		return true;
	}


	componentWillUnmount() {
		this.setState({});
	}

	render() {
	const networkDetails = [];	
	networkDetails.push(["Selected Search Name:",this.props.data.Name,"Description", this.props.data.Description]);
	networkDetails.push(["Requester:",this.props.data.Requester,"Requested At", this.props.data.RequestedAt]);
	networkDetails.push(["Finished At:",this.props.data.FinishedAt,"Count", this.props.data.Count]);
	
	return (<div className="container-fluid">
			<form onSubmit={ ()=> this.handleSubmit }>
				<Table responsive striped>
					<CustomTableHeaders data={networkDetails} />
				<tbody>
				<tr className="success">
						<th >File Size (Min.)</th>
						<td ><input type="text" name="minFileSize" id="minFileSize" value={this.state.searchFilter.minFileSize}   onChange={this.handleChange} /></td>
						<th >File Size (Max.)</th>
						<td ><input type="text" name="maxFileSize" id="maxFileSize" value={this.state.searchFilter.maxFileSize}  onChange={this.handleChange}/></td>
				</tr>
				<tr className="info">
					<th>File Path</th>
					<td ><input type="text" name="filePath" value={this.state.searchFilter.filePath}  onChange={(e)=> this.handleChange(e)} /></td>
					<th></th>
					<th></th>
				</tr>
				<tr className="info">
					<th>Last Used Date (Min.)</th>
					<th>
					<input type="text" id="minDate" name="minDate" value={ this.state.searchFilter.minDate }  onChange={this.handleChange} />
					<br/>(YYYY-MM-DD) 
					</th>
					<th>Last Used Date (Max)</th>
					<th>
					<input type="text" id="maxDate"  name="maxDate"  value={ this.state.searchFilter.maxDate }  onChange={ this.handleChange } />
					<br/>(YYYY-MM-DD) 
					</th>
				</tr>
				<tr className="info">
					<td align="center" colSpan={4}>
						<Button bsStyle="primary" onClick={(e)=> this.handleSubmit(e)} disabled={this.props.isLoading}>{ this.props.isLoading ? "Loading....." : 'Submit'}</Button>
					</td>
				</tr>
				<tr>
					<td colSpan={4} className="danger">
						<b><i>Specify search criteria and Submit.
						System performs search in batch of {this.props.pageSize} records.
						To perform search on specific batch, select Page No appearing below after first submit.</i></b>
					</td>
				</tr>
			</tbody></Table></form>
			</div>
			)
	}

  
}

const CustomTableHeaders = (props) => {
	return (<thead>
				<tr className="darkblue" >
						<th colSpan={4}>Selected Search </th>
				</tr>
				{ props.data.map( (item, index) => <tr className="warning" key={index}>{item.map( (item, index) => <th key={index}> {item}</th>)}</tr>) }
			</thead>);
}

SearchForm.propTypes = {
	pageSize: PropTypes.number.isRequired,
	isLoading: PropTypes.bool.isRequired,
	onSubmitHandler: PropTypes.func.isRequired,
	data: PropTypes.shape({
			Name: PropTypes.string.isRequired,
			Description: PropTypes.string.isRequired,
			Requester: PropTypes.string.isRequired,
			RequestedAt: PropTypes.string.isRequired,
			FinishedAt: PropTypes.string.isRequired,
			Count: PropTypes.number.isRequired
	})
};