import React, { Component } from 'react';
import { toast }  from 'react-toastify';
import { Grid } from 'react-bootstrap'; 

import _ from 'lodash';

import { SearchSideBar, ShowFilesList, Pagination } from '../utils/sfcmethods.js';
import SearchForm from './searchform.js';
import httpService from '../services/searchservice.js';

class Searches extends Component {

	state = {
		searchData: [],
		resultsData: [],
		selectedValue: -1,
		filepath:"",
		pageNo:1,
		pageSize: 1000,
		recordCount: 0,
		sortBy:"Path",
		isLoading:false,
		searchCriteria: {}
	}

	db = null;  

	constructor(props) {
		super(props);
		this.onChangeHandler = this.onChangeHandler.bind(this);
		this.onSubmitHandler = this.onSubmitHandler.bind(this);
		this.onPageChangeHandler = this.onPageChangeHandler.bind(this);
		this.onSortHandler = this.onSortHandler.bind(this);
		}

	async componentDidMount() {
		try {
			const { data } = await httpService.getSearchList();
			this.setState({ searchData: data });
			this.cleanSearchCriteria();


		}catch(err) {
			alert(err);
		}
	}

	cleanSearchCriteria() {
		this.setState({ searchCriteria: { minFileSize: 0, maxFileSize: 0, filePath: '', minDate:'' , maxDate:''  }});
	}

	//Sorting Functionality
	onSortHandler = (columnName)=> {
		this.setState({ resultsData: _.sortBy(this.state.resultsData, columnName), sortBy: columnName } );
	}

	//Network Path change
	onChangeHandler = async (lineNo)=> {
		this.setState( { selectedValue: lineNo, resultsData:[], pageNo: 1, isLoading: false, start:1 } );
		this.cleanSearchCriteria();
	}

	//Invoked from Page change click 
	onPageChangeHandler = async (event)=> {
		 event.preventDefault();
		 this.setState({isLoading: true});
		 const pageNo=event.target.value;
		 setTimeout(()=>this.populateResults(pageNo), 500);
	}

	//Invoked from Search Submit
	onSubmitHandler = async (searchCriteria) => {
		this.setState({isLoading: true, searchCriteria, start: 1});
		setTimeout(()=>this.populateResults(), 500);
	}

	getExtension( data ) {
		return data.toLowerCase().substr(data.lastIndexOf(".")); 
	}

	getMaxDate( prm_date_1, prm_date_2 ) {
		let date_1 = '';
		let date_2 = '';
		
		if( !prm_date_1 ) return new Date(prm_date_2);
		if( !prm_date_2 ) return new Date(prm_date_1);

		date_1 = new Date(prm_date_1);
		date_2 = new Date(prm_date_2);
		return (( date_1 > date_2 ) ?  date_1 : date_2);
	}

	mapResultsToView = (data)=> {
		return data.map( (item, index) => {
			item.Ext = this.getExtension(item.Path);
			item.MaxDate = this.getMaxDate(item.LastModified, item.Created);
			return item;
		});
	}
	
	objectToString(item) {
		return JSON.stringify(item);
	}

	componentWillUnmount() {
		this.setState({});
	}
	


	//Filter data based on user screen inputs
	applyFiltersOnBatch(){
		try {
				let { minFileSize, maxFileSize, filePath, minDate, maxDate } = this.state.searchCriteria;
				 minFileSize= minFileSize ? minFileSize : 0;
				 maxFileSize= maxFileSize ? maxFileSize : 0;
				 filePath = filePath ? filePath.toLowerCase() : '';
				 minDate=(minDate ? new Date(minDate) : '');
				 maxDate=(maxDate ? new Date(maxDate) : '');
				 if (maxDate) {
				 	maxDate.setDate(maxDate.getDate()+1);
				 }
				 
				return	(
						this.state.resultsData.filter( item => item.Size >= minFileSize )
						.filter( item =>  item.Size <= ( maxFileSize === 0 ? item.Size : maxFileSize ) )
						.filter( item =>  ( filePath === '' || item.Path.toLowerCase().indexOf( filePath ) !== -1 ))
						.filter( item =>  ( minDate  ? item.MaxDate >= minDate : 1 ))
						.filter( item =>  { return ( maxDate  ? item.MaxDate < maxDate : 1 );})
						);
		} catch ( err ) {
			throw err;
		}
	};

	
	populateResults = async (pageNo)=> {
			try {
				let start=1;

				if(!pageNo)
					pageNo = this.state.pageNo;

				start = ((pageNo - 1) * this.state.pageSize) + 1 
				
				let { data } = await httpService.getResultList({ indexId: this.state.selectedValue, start, pageSize: this.state.pageSize});
				//localStorage.setItem( this.state.selectedValue.toString(), JSON.stringify(data));
				//localStorage.setItem( this.state.selectedValue.toString(), JSON.stringify(data));
				//data.forEach( item => document.getElementById("dataarea").value+=`$${JSON.stringify(item)}`);
				//data = data.slice(1,10);
				this.setState( { resultsData: _.sortBy(this.mapResultsToView(data), this.state.sortBy), pageNo, isLoading: false } );
				
			} catch(err) {
				toast.error("An error occured while fetching data"+err);
				this.setState( { isLoading: false } );
			}

	}

	render() {
		
		const selectedResult = this.state.searchData[parseInt(this.state.selectedValue)];
		const pageDetails = { pageNo: parseInt(this.state.pageNo), 
					  pageSize: this.state.pageSize, 
					  recordCount: 0,
					  loadedFiles: this.state.resultsData.length } ;
		
		const filteredData = this.applyFiltersOnBatch();

		if (selectedResult) 
			pageDetails.recordCount=selectedResult.Count

		return (<React.Fragment>
					<Grid>
						<div className="row">
						<div className="col-xs-12">
							<h1> [Company Name] File Search Application 
						</h1>
						</div>
						</div>
						<div className="row">
							<div className="col-xs-3">
							<textarea id="dataarea" style={{zIndex: 100}}></textarea>
							<SearchSideBar data={this.state.searchData} 
											onChangeHandler={this.onChangeHandler} />
							</div>
							<div className="col-xs-9">
							{selectedResult && <SearchForm data={selectedResult} 
											onSubmitHandler={this.onSubmitHandler} 
											selectedValue={this.state.selectedValue}
											isLoading = {this.state.isLoading}
											pageSize = {pageDetails.pageSize}
											pageDetails = { pageDetails } 
											/>}
							</div>
						</div>
					<div className="row">
						<div className="col-xs-3"></div>
						<div className="col-xs-9">
						{ (pageDetails.loadedFiles > 0) && <Pagination  pageDetails={pageDetails} onPageChange={this.onPageChangeHandler}/> }
						</div>
					</div>
					<div className="row">
						<div className="col-xs-12">
						{selectedResult && <ShowFilesList  data={filteredData} onSortHandler={this.onSortHandler}/> }
						</div>
					</div>
				</Grid>

				</React.Fragment>
				);
		
		}
}

export default Searches;